
.popular {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.head{
    font-weight: 600;
    font-size: 50px;
    text-transform: uppercase;

}
.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.popular_item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}
